import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { catchError, map, switchMap, takeUntil, tap, withLatestFrom } from 'rxjs/operators';
import { of } from 'rxjs';

import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Store } from '@ngrx/store';

import { Loan } from '@backbase/loans-collection-ui-ang';
import { NotificationService } from '@backbase/ui-ang/notification';

import { LoansStoreState } from './store.model';
import { catchErrorWithLog } from '../../utils/operators.util';
import {
  fetchAmountOfLoans,
  fetchAmountOfLoansError,
  fetchAmountOfLoansSuccess,
  fetchLoanById,
  fetchLoanByIdError,
  fetchLoanByIdSuccess,
  loadLoans,
  loadLoansError,
  loadLoansSuccess,
  updateLoanAlias,
  updateLoanAliasCancel,
  updateLoanAliasError,
  updateLoanAliasSuccess,
} from './actions';
import { selectLoan, selectLoansTotalCount } from './selectors';
import { ArrangementsService } from '../../services/arrangements.service';
import { LoansService } from '../../services/loans.service';
import { notificationsTitlesConfig } from '../../config/notifications.config';
import { setLoan } from '@backbase/loans-payment-config-ang';

@Injectable()
export class LoansEffects {
  constructor(
    private readonly actions$: Actions,
    private readonly store: Store<LoansStoreState>,
    private readonly loansService: LoansService,
    private readonly arrangementsService: ArrangementsService,
    private readonly notificationService: NotificationService,
    private readonly router: Router,
  ) {}

  fetchLoans$ = createEffect(() =>
    this.actions$.pipe(
      ofType(loadLoans),
      switchMap(({ page, append }) =>
        this.loansService.getLoans(page).pipe(
          map((response) => loadLoansSuccess({ response, append })),
          catchErrorWithLog((error) => of(loadLoansError({ error: error.message || 'Loans fetch error' }))),
        ),
      ),
    ),
  );

  redirectToDetailsFromList$ = createEffect(
    () =>
      this.actions$.pipe(
        ofType(loadLoansSuccess),
        tap(({ response: { totalCount, items } }) => {
          if (totalCount === 1) {
            this.router.navigateByUrl('loans/details/' + items[0].id);
          }
        }),
      ),
    { dispatch: false },
  );

  fetchLoanById$ = createEffect(() =>
    this.actions$.pipe(
      ofType(fetchLoanById),
      switchMap(({ id }) =>
        this.loansService.getLoanById(id).pipe(
          map((loan) => {
            setLoan(loan);

            return fetchLoanByIdSuccess({ loan });
          }),
          catchErrorWithLog((error) => of(fetchLoanByIdError({ error: error.message || 'Loan fetch error' }))),
        ),
      ),
    ),
  );

  fetchAmountOfLoans$ = createEffect(() =>
    this.actions$.pipe(
      ofType(fetchAmountOfLoans),
      withLatestFrom(this.store.select(selectLoansTotalCount)),
      switchMap(([, totalCountFromStore]) => {
        if (totalCountFromStore !== undefined) {
          return of(fetchAmountOfLoansSuccess({ totalCount: totalCountFromStore }));
        }

        return this.loansService.getLoansAmount().pipe(
          map((totalCount) => fetchAmountOfLoansSuccess({ totalCount })),
          catchError(() =>
            this.loansService.getLoans(0).pipe(
              map(({ totalCount }) => fetchAmountOfLoansSuccess({ totalCount })),
              catchErrorWithLog(() => of(fetchAmountOfLoansError())),
            ),
          ),
        );
      }),
    ),
  );

  updateLoanAlias$ = createEffect(() =>
    this.actions$.pipe(
      ofType(updateLoanAlias),
      withLatestFrom(this.store.select(selectLoan)),
      switchMap(([{ alias }, loan]) => {
        const id = (loan as Loan).id;

        return this.arrangementsService.updateUserPreferences(id, alias).pipe(
          map(() => updateLoanAliasSuccess({ alias, id })),
          catchError((error) => of(updateLoanAliasError({ error: error.message || 'Update alias error' }))),
          takeUntil(this.actions$.pipe(ofType(updateLoanAliasCancel))),
        );
      }),
    ),
  );

  updateLoanAfterAliasUpdate$ = createEffect(() =>
    this.actions$.pipe(
      ofType(updateLoanAliasSuccess),
      map(({ id }) => fetchLoanById({ id })),
    ),
  );

  notifyAliasUpdateSuccess$ = createEffect(
    () =>
      this.actions$.pipe(
        ofType(updateLoanAliasSuccess),
        tap(({ alias }) =>
          this.notificationService.showNotification({
            header: alias
              ? notificationsTitlesConfig.updateAliasSuccess
              : notificationsTitlesConfig.updateAliasToDefaultSuccess,
            message: '',
            modifier: 'success',
          }),
        ),
      ),
    { dispatch: false },
  );

  notifyAliasUpdateError$ = createEffect(
    () =>
      this.actions$.pipe(
        ofType(updateLoanAliasError),
        tap(() =>
          this.notificationService.showNotification({
            header: notificationsTitlesConfig.updateAliasError,
            message: '',
            modifier: 'error',
          }),
        ),
      ),
    { dispatch: false },
  );
}
